$main-color:#2d4341;
$second-color:#4b978b;



//class styles start here//


.landing-banner-container{margin: 2.5%;}


.video-banner-container{
  width: 100%;
  height: 90vh;
  border: 5px solid $second-color;
  overflow: hidden;
  position: relative;
  button{
    padding:2.5%;
    border-radius:15vw;
    transition: 0.5s;
    font-size: 1.5vw;
    background-color: white;
    &:hover{
      color: white;
      background-color: transparent;
      border: solid white;
      transform: scale(110%);
      
    }
    &:active{
      background-color: inherit;
      transform: scale(90%);
    }
  }
  video{
    width: 100%;
    height: 100%;
    object-fit: fill;
  }
  .popup{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 8%;
    left: 0%;
    display: flex;
    justify-content: center;
    z-index: 5;
    opacity: 0.98;
    div{
      width: 40%;
      height: 90%;
      color: whitesmoke;
      text-shadow: 0px 0px 10px black;
      border:  solid $second-color;
      font-size: 1.3vw;
      button{
        display: block;
        margin: auto;
      }
    }
    .catering{
      position: relative;
      background-color: #2d4341;
      span{
        max-width: 100%;
        text-align: center;
        display: block;
        padding: 5%;
        
        button{
          top:25%;
        }
      }
    }
    .business-hours{
      background-color: $second-color;
      span{
        margin-top: 50%;
        max-width: 100%;
        text-align: center;
        display: block;
        padding: 5%;
       
      }
    }
  }
  
}

.video-overlay{
  display: block;
  position: absolute;
  left: 25%;
  top: 50%;
  right: 25%;
  bottom: 25%;
  font-size: 1.5vw;
  z-index: 1;
  text-align: center;
  color: whitesmoke;
  text-shadow: 0px 0px 5px #181818;
}


.close-button{
  background-color: transparent !important;
  margin-left: 90% !important;
  border: none !important;
  &:hover{
    background-color: transparent !important;
  }
  
}
.details-container{
  margin-top: 10%;
  div{
    width: 100%;
    height: 80vh;
    margin-top: 10%;
    margin-bottom: 5%;
    display: flex;
    justify-content: center;
    figure{
    display: flex;
    justify-content:center ;
    width: 55%;
    height: 100%;
    img{
      border: 3px solid #4b978b;
      width: 60%;
      object-fit: fill;
    }
    figcaption{
      width: 40%;
      height: 100%;
      overflow: hidden;
      font-size: larger;
      padding: 2.5%;
      display: block;
      h1{
        font-size: 3vw;
        font-weight: bolder;
        color: #2d4341;
      }
      h3{
        color: #2d4341;
        font-size: 1.5vw;
      }
      ul{
        margin-top:80%;
        margin-left: 5%;
        font-size: 1vw;
        line-height: 1.5;
        b{color: #2d4341;}
      }
     }
    }
    
  }
 
}



.features-container{
  ul{
    margin-top: 35% !important;
  }
}