/* To specify background color and border */
$main-color:#2d4341;
$second-color:#4b978b;

.reservation-page{
    display: flex;
    height: 80vh;
    justify-content: space-evenly;
    background-color: $second-color;
    font-size: 1.5vw;
    padding-top:20vh ;
    form{
        background-color: $second-color;
        width: 45%;
        height: 56vh;
        display: grid;
        color:white;
        font-weight: bolder;
        justify-content: space-between;
        grid-template-columns: auto auto;
        column-gap: 2vw;
        row-gap: 1.5vw;
        button{
            font-size: 4vw;
            margin:auto;
            width: 80%;
            font-weight: bolder;
            background-color: white;
            border: solid $second-color;
            &:hover{
                color: white;
                border: solid white;
                background-color: $second-color;
            }
        }
        label{
            input , textarea{
               color: white;
                width: 100%;
                font-size: 1.8vw;
                height: 5vh;
                border: solid white;
                background-color: transparent;
                
                &::placeholder{
                    color: rgba(199, 199, 199, 0.512);
                }
            }
            textarea{
                height: 20vh;
            }
        }
    }
}

#calendar{
    width:50%;
    header{
        display: block;
        width: 100%;
        height: 10vh;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: white;
    }
    button{
        background-color: transparent;
        color: white;
        font-size: 4vw;
        border: none;
    }
    table{
        
        width: 100%;
        border-collapse: collapse;
        margin-top: 1%;
        th , td{
          padding: 1.5% 3% 1.5% 3% ;
          text-align: center;
          border: 1px solid white; 
          color: white; 
        }
        th{
            background-color: $second-color;
            color: white;
        }
        td.selected , td:hover{
            background-color: white;
            color:$second-color
        }

    }
}
