$main-color:#2d4341;
$second-color:#4b978b;


.menu-page{
    display: block;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    .menu{
        margin-bottom: 2%;
        margin-top: -0%;
        height: fit-content;
        border: solid $main-color;
        font-size: 1vw;
        span{
            
            border-bottom: 2px solid $main-color;
            h1 ,h2 ,h3{
                padding: 2% 0% 2% 0%;
                display: block;
                border-right: 1px solid black;
                color: $main-color;
            }
            h3{
                border: none;
            }

        }
    }
    button{
        padding: 1vw;
        margin-left: 0.5%;
        border-radius: 10%;
        background: transparent;
        color:$main-color;
        &:hover{
            background-color: $second-color;
            color: white;
        }
    }
    .active{
            background-color: $second-color;
            color: white;
        }
    span{
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        h1 , h2 , h3{
            width: 33%;
        }
    }
}