$main-color:#2d4341;
$second-color:#4b978b;



.catering-page{
    height: 100vh;
    background-color: $second-color;
    #calendar{
        float: right;
    }
    form{
        margin: 8%;
        padding: 2%;
        height: 60vh;
        float: left;
        width: 30%;
        display: grid;
        grid-template-columns: auto auto;
        column-gap: 2vw ;
        justify-content: space-between;
        background-color: $main-color;
        color:white;
        label{
            display: block;
            width: 100%;
            font-size:1vw;
            
            input , select ,textarea{
                height: 50%;
                width: 100%;
                border: 1px solid white;
                background-color: $second-color;
                color: white;
                font-size: 1vw;
            }
        }
        button{
            font-size: 1.5vw;
            color:white;
            border: none;
            background-color: $second-color;
        }
    }
}
